import React from 'react';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import { graphql } from 'gatsby';

import Footer from './footer';
import Navbar from './navbar';
import SEO from './seo';
import LanguageManager from './languageManager';

interface Props {
  pageContext: {
    locale: string;
  };
  data: {
    mdx: {
      id: string;
      body: string;
      frontmatter?: {
        noindex?: boolean;
        metadata: {
          title: string;
          description: string;
          image: {
            publicURL: string;
          };
        };
        hero: object;
        concept: object[];
        showcase: object[];
      };
    };
  };
}

const Layout = ({ data, pageContext }: Props) => {
  const { locale } = pageContext;
  return (
    <div className="relative overflow-hidden">
      <LanguageManager />
      {!data.mdx.frontmatter.noindex && (
        <SEO
          title={data.mdx.frontmatter?.metadata.title}
          description={data.mdx.frontmatter?.metadata.description}
          image={data.mdx.frontmatter?.metadata.image}
          locale={locale}
        />
      )}
      <Navbar />
      <MDXRenderer frontmatter={data.mdx.frontmatter} locale={locale}>
        {data.mdx.body}
      </MDXRenderer>
      <Footer locale={locale} />
    </div>
  );
};

export default Layout;

export const pageQuery = graphql`
  query MDXQuery($id: String!) {
    mdx(id: { eq: $id }) {
      id
      body
      frontmatter {
        noindex
        metadata {
          title
          description
          image {
            publicURL
          }
        }
        hero {
          title
          description
          whitepaperButton
          waitlistButton
        }
        video {
          title
          description
          url
        }
        showcase {
          title
          products {
            title
            description
            image {
              publicURL
            }
          }
        }
        concept {
          title
          steps {
            description
            image {
              publicURL
            }
          }
        }
        whitepaper {
          title
          description
          file {
            publicURL
          }
          pdfButton {
            label
          }
        }
        newsletter {
          title
          description
          form {
            firstNameLabel
            emailLabel
            buttonLabel
            successLabel
          }
          success {
            title
            description
            buttonLabel
          }
        }
      }
    }
  }
`;
